/* =====================================================================================
Variables */
/* =====================================================================================
Basic styles */
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "HaginCaps", "Helvetica", sans-serif;
  font-size: 18px;
  line-height: 18px;
  font-weight: 400;
  color: #817155; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

* {
  box-sizing: border-box; }

/* =====================================================================================
Container */
.container {
  max-width: 1366px;
  width: 100%;
  margin: 0 auto;
  padding-top: 0;
  padding-right: 42px;
  padding-bottom: 0;
  padding-left: 53px; }

/* =====================================================================================
Media max-width: 1150px*/
@media (max-width: 1150px) {
  .container {
    padding-right: 15px;
    padding-left: 15px; } }

/* =====================================================================================
Visually-hidden */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* =====================================================================================
Main-btn */
.main-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 240px;
  width: 100%;
  min-height: 60px;
  font-size: 24px;
  line-height: 30px;
  text-transform: uppercase;
  color: #baa480;
  border: 1px solid #baa480; }

.main-btn:hover,
.main-btn:focus {
  opacity: 0.8; }

.main-btn:focus {
  opacity: 0.5; }

/* =====================================================================================
Media max-width: 900px*/
@media (max-width: 900px) {
  .main-btn {
    max-width: 196px;
    width: 100%;
    min-height: 50px;
    font-size: 22px;
    line-height: 30px; } }

/* =====================================================================================
Header-menu */
.sidebar-menu {
  position: fixed;
  top: 0;
  left: -100%;
  z-index: 5;
  width: 95%;
  min-height: 100vh;
  box-shadow: 4px 0 20px #171716;
  background-color: #f0ede8;
  transition: all linear 0.5s; }

.sidebar-menu--opened {
  left: 0; }

.body--nojs .sidebar-menu {
  position: static;
  width: 100%;
  min-height: 450px;
  box-shadow: none; }

.sidebar-menu__inner {
  margin-top: 18px; }

.body--nojs .sidebar-menu__inner {
  margin-top: 0;
  padding-top: 15px; }

.sidebar-menu__user-block {
  display: flex;
  justify-content: space-between; }

.sidebar-menu__btn-close svg {
  width: 14px;
  height: 14px;
  fill: #9b8869; }

.body--nojs .sidebar-menu__btn-close {
  display: none; }

.sidebar-menu__btn-close:focus svg,
.sidebar-menu__btn-close:hover svg {
  fill: #d89527; }

.sidebar-menu__btn-close:active svg {
  fill: #ff9e00; }

.sidebar-menu__user-name {
  margin-left: 8px;
  font-family: "Roboto", "Helvetica", sans-serif;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.7px;
  color: #9b8869; }

.sidebar-menu__user-name svg {
  width: 12px;
  height: 15px;
  margin-right: 5px;
  fill: #9b8869; }

.sidebar-menu__user-logout {
  width: 16px;
  height: 18px;
  fill: #ba4f4f; }

.sidebar-menu__list {
  margin-top: 49px;
  margin-bottom: 0;
  padding-left: 15px;
  list-style: none; }

.sidebar-menu__list-item {
  margin-bottom: 20px; }

.sidebar-menu__list-item a {
  font-size: 18px;
  line-height: 18px;
  letter-spacing: 1.3px;
  color: #9b8869; }

.sidebar-menu__list-item a:focus,
.sidebar-menu__list-item a:hover {
  color: #d89527; }

.sidebar-menu__list-item a:active {
  color: #ff9e00; }

.sidebar-menu__list-item--basket {
  margin-bottom: 17px;
  padding-top: 20px;
  padding-bottom: 20px;
  border-top: 1px solid #9b8869;
  border-bottom: 1px solid #9b8869; }

.sidebar-menu__list-search svg {
  width: 16px;
  height: 16px;
  margin-right: 5px;
  fill: #9b8869; }

/* =====================================================================================
Header */
.header {
  position: fixed;
  top: 0;
  width: 100%;
  padding-top: 37px;
  color: #9b8869; }

.body--nojs .header {
  position: static;
  padding-top: 0;
  background-color: #171515; }

.header__inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center; }

.body--nojs .header__inner {
  padding-top: 10px;
  padding-bottom: 10px; }

.header__burger-btn {
  display: none; }

.header__logo {
  display: block; }

.header__logo svg {
  display: block;
  width: 184px;
  height: 30px;
  fill: #baa480; }

/* =====================================================================================
Media max-width: 1050px*/
@media (max-width: 1150px) {
  .header {
    padding-top: 17px; }
  .header__logo svg {
    width: 130px;
    height: 22px; } }

/* =====================================================================================
Media max-width: 900px*/
@media (max-width: 900px) {
  .header__burger-btn {
    display: block; }
  .header__burger-btn svg {
    fill: #9b8869; }
  .header__burger-btn:focus svg,
  .header__burger-btn:hover svg {
    fill: #d89527; }
  .header__burger-btn:active svg {
    fill: #ff9e00; } }

/* =====================================================================================
Main-nav */
.main-nav {
  max-width: 741px;
  width: 100%; }

.main-nav__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

.main-nav__item a {
  font-size: 18px;
  line-height: 18px;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  color: #9b8869; }

.main-nav__item a:focus,
.main-nav__item a:hover {
  color: #d89527; }

.main-nav__item a:active {
  color: #ff9e00; }

/* =====================================================================================
Media max-width: 1050px*/
@media (max-width: 1150px) {
  .main-nav {
    max-width: 630px; }
  .main-nav__item a {
