.user-nav__item--basket {
  margin-left: 23px; }

.user-nav__item--basket svg {
  width: 22px;
  height: 19px; }

.user-nav__item a:focus svg,
.user-nav__item a:hover svg {
  fill: #d89527; }

.user-nav__item a:active svg {
  fill: #ff9e00; }

/* =====================================================================================
Media max-width: 1150px*/
@media (max-width: 1150px) {
  .user-nav__item--search,
  .user-nav__item--basket {
    margin-left: 15px; }
  .user-nav__item--user svg {
    width: 16px;
    height: 19px; }
  .user-nav__item--search svg {
    width: 16px;
    height: 16px; }
  .user-nav__item--basket svg {
    width: 19px;
    height: 17px; } }

/* =====================================================================================
Media max-width: 900px*/
@media (max-width: 900px) {
  .user-nav__item {
    margin-left: 0; }
  .user-nav__item--user,
  .user-nav__item--search {
    display: none; } }

